<template>
     <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
 <ProductFilter :price-from.sync="filterPriceFrom"  :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId"/>
        <section class="catalog">
<div v-if="productsLoading">Загрузка товаров...</div>
<div v-if="productsLoadingFailed">произошла ошибка при загрузке товаров <button @click.prevent="loadProducts"> Попробовать еще разок</button></div>

<ProductList :products="products" > </ProductList>
<basePagination v-model="page" :count="countProducts"  :per-page="productsPerPage"/>

        
      </section>
      
    </div>
  </main>
</template>
<script>
// import products from '@/data/products.js'; удаляем после подключения сервера
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios  from 'axios';
import {API_BASE_URL} from '@/config';



export default (await import('vue')).defineComponent ({
  
  components: {ProductList, BasePagination, ProductFilter},
  data() {
  return {
    filterPriceFrom: 0,
    filterPriceTo: 0,
    filterCategoryId: 0,
    page:1,
    productsPerPage: 3,

    productsData: null,

    productsLoading: false,
    productsLoadingFailed: false,
  }

  },
  computed : {

    //пишем код заново из-за сервера
    // filteredProducts() {
    //   let filteredProducts = products;
    //   if(this.filterPriceFrom > 0 ) {
    //     filteredProducts = filteredProducts.filter(product => product.price > this.filterPriceFrom);
    //   }
    //   if(this.filterPriceTo > 0 ) {
    //     filteredProducts = filteredProducts.filter(product => product.price < this.filterPriceTo);
    //   }
    //    if(this.filterCategoryId ) {
    //     filteredProducts = filteredProducts.filter(product => product.categoryId === this.filterCategoryId);
    //   }
    //   return filteredProducts
    // },
    products() { 
      //если есть товары, то возвращаем список, а если нет, то пустой массив
      return this.productsData 
      ? this.productsData.items.map(product => {
        return {
          //Тот же продукт ... product и меняем картинку
          ...product,
          image: product.image.file.url
        }
      }) : [];
      //удаляем, так как подключаем данные с сервера в 7 уроке
    //   const offset = (this.page - 1)* this.productsPerPage;
    // return this.filteredProducts.slice(offset, offset + this.productsPerPage);
    },
    countProducts() {

      return this.productsData ? this.productsData.pagination.total : 0;
      //изменяем после подключения сервера
      // return this.filteredProducts.length;
    }

  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
       this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
axios.get(API_BASE_URL + '/api/products', {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filterCategoryId,
          minPrice: this.filterPriceFrom,
          maxPrice:  this.filterPriceTo

        }
      })
      .then(response => this.productsData = response.data)
      .catch(() => this.productsLoadingFailed = true )
      .then(() => this.productsLoading = false ); 

    }, 0);
      }
    },
    //все пихаем сюда, что будем передавать, например фильтр будет вторым параметром
    loadProducts() {
      
    //   пример  axios.get(`https://vue-study.skillbox.cc/api/products?page=${this.page}&limit=${this.productsPerPage}`
  },
  //добавляем наблюдатель, чтобы следить за обновлением товаров на странице. Наблюдать будем за page и если оно меняется, то вызываем loadProducts()
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
this.loadProducts();
    },
    filterPriceTo() {
this.loadProducts();
    },
    filterCategoryId() {
this.loadProducts();
    }
  },
  created() {
    this.loadProducts();
  }


})
</script>