<template>
     <li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120"  alt="item.product.title">
              </div>
              <h3 class="product__title">
              {{ item.product.title }}
              </h3>
              <p class="product__info">
                Объем: <span>128GB</span>
              </p>
              <span class="product__code">
                Артикул: {{ item.product.id}}
              </span>

              <div class="product__counter form__counter">
                <button type="button" aria-label="Убрать один товар" @click.prevent="deleteAmount">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>
        
                <input type="number" v-model.number="amount" name="count">

                <button type="button" aria-label="Добавить один товар"  @click.prevent="addAmount">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="product__price">
                {{(item.amount * item.product.price) | numberFormat}} ₽
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>

<script>

import numberFormat from '@/helpers/numberFormat';
import {mapActions, mapMutations} from 'vuex';

export default {
    filters: {numberFormat},
    props: ['item'],
    computed: {
        amount: {
            get() {
                return this.item.amount;
            },
            set(value) {
                this.$store.dispatch('updateCartProductAmount', {productID: this.item.productID, amount: value})
            }
        }
    },
    methods: {
        ...mapMutations({deleteProduct: 'deleteCartProduct'}),
        ...mapActions(['deleteProductfromCart']),
        deleteProduct() {
          this.deleteProductfromCart( this.item.productID)
        },
        addAmount(){
            this.amount++;
        },
        deleteAmount(){
            if(this.amount > 1) {
                this.amount--;
            }
            
        }
       
    }
    
}
</script>
