import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

// сообщаем vue, что будем и-ть vuex
Vue.use(Vuex);
// создадим новое хранилище
export default new Vuex.Store({
    state: {
        cartProducts: [],
        userAccessKey: null,
        cartProductsData: [],
        orderInfo: null,
        productAddSending: false,
        loadingCart: true,
        loadOrderInfo: true,
    },
    mutations: {
        //длинный вариант записи 
        // addProductToCart(state, payload) {
        //     state.cartProducts.push({
        //         productID: payload.productID,
        //         amount: payload.amount,
        //     });
        // }

        //короткий вариант
        // addProductToCart(state, {productID, amount}) {

        //     const item = state.cartProducts.find(item => item.productID === productID);
        //     if (item) {
        //         item.amount += amount;

        //     }else {
        //         state.cartProducts.push({
        //             productID,
        //             amount,
        //         });
        //     }     
        // },
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        resetCart(state) {
            state.cartProducts = [];
            state.cartProductsData = [];
        },
        updateCartProductAmount(state, {productID, amount}) {
            const item = state.cartProducts.find(item => item.productID === productID);
            if(item) {
                item.amount = amount;
            }
        },
        deleteCartProduct(state, productID) {
            state.cartProducts = state.cartProducts.filter(item => item.productID !== productID);
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        //синхронизация отображения данных в мутациях :)
        syncCartProducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    //берем на уровень глубже
                    productID: item.product.id,
                    amount: item.quantity,
                };
            });
        },
        productAddSendingStatus(state, value) {
            state.productAddSending = value;
          },
          loadCartStatus(state, value) {
            state.loadingCart = value;
          },
          loadOrderInfoStatus(state, value) {
            state.loadOrderInfo = value;
          },
       
    },
    getters: {
        orderInfo(state) {
            return state.orderInfo;
          },
        cartDetailProducts(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productID).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        image: product.image.file.url
                    }
                };
            });
        },
        orderInfoBasket(state) {
            return state.orderInfo ? state.orderInfo.basket.items.map((item) => ({
              ...item,
              productID: item.product.id,
              amount: item.quantity,
            
            })) : [];
          },
        cartTotalPrice(state, getters) {
           return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        }
    },
    //есть специальные действия, ф-ции, которые не ограничены синхронностью/асинхронностью
    actions: {
        loadOrderInfo(context, orderId) {
            return (new Promise((resolve) => setTimeout(resolve, 1000)))
            .then(()=> {  
            axios.get(API_BASE_URL + '/api/orders/' + orderId, {
                params: {
                 userAccessKey: context.state.userAccessKey
                }
             })
             .then(response => {
                context.commit('updateOrderInfo', response.data);
                context.commit('loadOrderInfoStatus', false);
             })
             .catch(() => router.replace({ name: 'notFound' }));
            });
        },
        loadCart(context) {
           return  axios
            .get(API_BASE_URL + '/api/baskets', {
               params: {
                userAccessKey: context.state.userAccessKey
               }
            })
            .then( response => { 
                if(!context.state.userAccessKey) { 
                localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
                }
                context.commit('updateCartProductsData', response.data.items);
                context.commit('syncCartProducts');
                context.commit('loadCartStatus', false);
        });
    },
    addProductToCart(context, {productID, amount}) {
        //Добавляем промис, чтобы проверить, что товар добавляется в корзину и сам код вызываем уже когда промис сработает
        return (new Promise(resolve => setTimeout(resolve, 2000)))
       .then(() => {
         return axios
        .post(API_BASE_URL + '/api/baskets/products', {
            productId: productID,
            quantity: amount
        }, { 
            params: {
                userAccessKey: context.state.userAccessKey
            }
        })
        .then(response => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
        });
       });
    },
    updateCartProductAmount(context, {productID, amount}) {
        //здесь просто вызываем жту мутацию, без запросов/ответов от сервера.
        context.commit('updateCartProductAmount',{productID, amount} );
        if(amount < 1) {
            //прерываем действие и не выполняем запрос( когда стираем количество, например)
            return;
        }
        return axios
        .put(API_BASE_URL + '/api/baskets/products', {
            productId: productID,
            quantity: amount
        }, { 
            params: {
                userAccessKey: context.state.userAccessKey
            }
        })
        .then(response => {
            context.commit('updateCartProductsData', response.data.items);
          
        })
        .catch(() => {
            context.commit('syncCartProducts');
        });
    },
    deleteProductfromCart(context, productID) {
        return axios
        .delete(API_BASE_URL + '/api/baskets/products', {
           data: {
            productId: productID,
           },
            params: {
                userAccessKey: context.state.userAccessKey
            },
        })
        .then(response => {
            context.commit('updateCartProductsData', response.data.items);
            context.commit('syncCartProducts');
        });
    }
}
});