<template>
 <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item" >
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <AppPreloader v-if="formSending" />
        <div class="cart__field" v-else>
          <div class="cart__data">
            <!-- <label class="form__label">
              <input class="form__input" v-model="formData.name" type="text" name="name" placeholder="Введите ваше полное имя">
              <span class="form__value">ФИО</span>
              <span class="form__error" v-if="formError.name">{{ formError.name }}</span>
            </label> -->
                <BaseFormText v-model="formData.name" :error="formError.name"  title="ФИО" placeholder="Введите ваше полное имя"></BaseFormText>
                <BaseFormText v-model="formData.address" :error="formError.address"  title="Адрес доставки" placeholder="Введите ваш адрес"></BaseFormText>
                <BaseFormText v-model="formData.phone" :error="formError.phone"  title="Телефон" placeholder="Введите ваш телефон"></BaseFormText>
                <BaseFormText v-model="formData.email" :error="formError.email"  title="Email" placeholder="Введите ваш Email"></BaseFormText>
                <BaseFormTextArea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания" ></BaseFormTextArea>
           
            <!-- <label class="form__label">
              <textarea class="form__input form__input--area"  v-model="formData.comments" name="comments" placeholder="Ваши пожелания"></textarea>
              <span class="form__value">Комментарий к заказу</span>
              <span class="form__error" v-if="formError.comments">{{ formError.comments }}</span>
            </label> -->
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

       <OrderList
          :products="products"
          :totalPrice="totalPrice"
          :amount="$store.state.cartProducts.length"
          :showButton="true" :loading="loadingCart"
        />
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          
          <p>
            {{formErrorMessage}}
         похоже, произошла ошибка, Попробуйте отправить снова или перезагрузите страницу
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormField from '@/components/BaseFormField.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from "@/components/BaseFormTextArea.vue";
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import AppPreloader from '@/components/AppPreloader.vue';
import OrderList from '@/components/OrderList.vue';
import {mapGetters} from 'vuex';
import numberFormat from '@/helpers/numberFormat';


export default {
   props: [ 
    'dictionary',
    'loadOrderInfo',
    'orderInfo'
  ],
    components: {BaseFormField, BaseFormText, BaseFormTextArea, AppPreloader, OrderList},
    data() {
        return {
            formData: {},
            formError: {},
            loadingCart: false,
            formErrorMessage: '',
            formSending: false,
        }
    },
    computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
  },
    filters: {
        numberFormat,
    },
    
    methods: {
        order() {
            this.formError = {};
            this.formErrorMessage = {};
            this.formSending = true;

      setTimeout(() => {
        axios.post(API_BASE_URL + '/api/orders', {
          ...this.formData,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
          .then((response) => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.formData = {};
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
          .then(() => { this.formSending = false; });
      }, 1000);
    },
  },
  watch: {
    '$store.state.loadingCart': {
      handler() {
        this.loadingCart = this.$store.state.loadingCart;
      },
      immediate: true,
    },
  },
}
</script>
